/* src/pages/ResultPage.css */

.result-page {
    display: flex;
    flex-direction: column;
    gap: 3vh;
    padding: 2vw;
    box-sizing: border-box;
    max-width: 100%;
    width: 100%;
    font-family: "Noto Sans KR", sans-serif;

    /* 배경 이미지 설정 */
    background-image: url('../assets/background.jpg');
    /* 경로는 실제 이미지 경로에 맞게 수정 */
    background-size: cover;
    /* 배경 이미지를 꽉 채움 */
    background-repeat: no-repeat;
    /* 반복하지 않음 */
    background-position: center;
    /* 중앙 정렬 */
}


.result-block {
    padding: 3vw;
    border-radius: 1rem;
    transition: background-color 0.3s;
}



.event-title {
    font-size: clamp(2.4rem, 5vw, 3.2rem);
    font-weight: bold;
    color: #0d47a1;
    text-align: center;
    font-family: "Hahmlet", serif;
    font-optical-sizing: auto;
    font-style: normal;

}

.course-image {
    margin-top: 2vh;
    text-align: center;
}

.course-image img {
    width: 70vw;
    height: auto;
    border-radius: 0.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}


.result-title {
    font-family: "Hahmlet", serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: normal;
    font-size: 1.8rem;
    color: #0d47a1;
    margin-bottom: 2vh;
    text-align: center;
}

.result-subtitle {
    font-family: "Hahmlet", serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: normal;
    font-size: 2.0rem;
    color: #1565c0;
    margin-top: 2vh;
    margin-bottom: 1vh;
}

.result-text {
    font-family: "Hahmlet", serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5rem;
    color: #333;
    line-height: 1.6;
    font-weight: 300;
}

.no-result {
    font-size: 1rem;
    color: #999;
    text-align: center;
    font-style: italic;
}

@media (max-width: 600px) {
    .result-title {
        font-size: 1.8rem;
    }

    .result-subtitle {
        font-size: 1.5rem;
    }

    .result-text {
        font-size: 1.1rem;
    }

    .result-block {
        padding: 5vw;
    }
}

/* 푸터 */
.footer {
    margin-top: 3rem;
    text-align: center;
    font-size: clamp(0.75rem, 2vw, 0.9rem);
    color: #607d8b;
    width: 100%;
}

.footer a {
    color: #1565c0;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}